.Pic img {
  max-height: 300px;
  max-width: 300px;
  vertical-align: middle;
  border: 2px solid gray;
  box-sizing: border-box;
  border-radius: 10px;
  grid-area: I;
  margin-top: 60px;
  margin-right: 8px;
}
.hidden {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  z-index: -1;
  position: relative;
}
.hidden + label {
  background: url(https://findicons.com/files/icons/1152/web_application/48/load.png)
    no-repeat;
  background-position: center;
  background-size: cover;
  height: 40px;
  width: 40px;
  display: inline-block;
  position: relative;
  margin: auto;
  vertical-align: bottom;
}

.hidden + label:hover {
  cursor: pointer;
  border-radius: 50%;
  background-color: rgb(57, 58, 57);
}
