.About {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: 30% 67%;
}
.info {
  color: rgb(126, 172, 21);
  display: block;
  max-width: 100px;
  margin-left: 10%;
  margin-top: 3%;
  font-size: 18px;
  padding-right: 2px;
  justify-self: center;
}
.MainAbout {
  background: rgb(34, 32, 32);
  font-size: 28px;
  color: rgb(126, 172, 21);
  margin: 1px;
  min-height: 600px;
}
.advertisement {
  margin-left: 5%;
  max-width: 300px;
  color: rgb(85, 99, 52);
  font-size: 20px;
  justify-self: center;
}
.heading {
  padding: 30px;
  font-family: MyFont3;
  color: darkgoldenrod;
  font-size: 35px;
}
